import { toggleIn } from '@everlutionsk/helpers';
import {
  Checkbox,
  CheckboxProps,
  FormControl,
  FormControlLabel,
  FormGroup as MuiFormGroup,
  FormHelperText,
  FormLabel,
  styled
} from '@mui/material';
import React, { memo, ReactNode } from 'react';

export interface CheckboxesInputProps<T = any> {
  /**
   * Input name.
   */
  readonly name?: string;

  /**
   * Selected values.
   */
  readonly value: T[];

  /**
   * Called when user toggles option.
   */
  readonly onChange: (value: T[]) => void;

  readonly onBlur?: (e: React.FocusEvent<any>) => void;

  readonly error?: string;

  /**
   * Options to render.
   */
  readonly options: ReadonlyArray<CheckboxesInputOption<T>>;

  /**
   * The label content.
   */
  readonly label?: ReactNode;

  /**
   * The helper text content.
   */
  readonly helperText?: ReactNode;

  /**
   * Display options in a compact row.
   */
  readonly row?: boolean;

  /**
   * If `true`, all checkbox elements will be disabled.
   */
  readonly disabled?: boolean;

  readonly className?: string;
}

export interface CheckboxesInputOption<T = any>
  extends Omit<CheckboxProps, 'name' | 'checked' | 'children'> {
  readonly label: ReactNode;
  readonly value: T;
}

type CheckboxesInput = <T>(props: CheckboxesInputProps<T>) => ReactNode;

/**
 * Material-UI checkboxes with shared array value.
 */
export const CheckboxesInput: CheckboxesInput = memo(props => {
  return (
    <FormControl component="fieldset" className={props.className} error={props.error != null}>
      {props.label && <FormLabel component="legend">{props.label}</FormLabel>}

      <FormGroup row={props.row}>
        {props.options.map(({ label, value, ...optionMuiProps }, i) => (
          <FormControlLabel
            key={i}
            label={label}
            control={
              <Checkbox
                {...optionMuiProps}
                name={props.name}
                onChange={(event, checked) => {
                  const updated = toggleIn(props.value ?? [], value);
                  props.onChange(updated);
                  optionMuiProps.onChange?.(event, checked);
                }}
                checked={(props.value ?? []).includes(value)}
                onBlur={event => {
                  props.onBlur?.(event);
                  optionMuiProps.onBlur?.(event);
                }}
                disabled={props.disabled ?? optionMuiProps.disabled}
              />
            }
          />
        ))}
      </FormGroup>

      {(props.error != null || props.helperText != null) && (
        <FormHelperText>{props.error ?? props.helperText}</FormHelperText>
      )}
    </FormControl>
  );
});

const FormGroup = styled(MuiFormGroup)`
  &.row {
    flex-direction: row;
  }
  &.column {
    flex-direction: column;
  }
`;
